@import "../variables";
@import "../mixins";

.promo {
  text-align: left;
  position: relative;

  color: $color-white;
  background-color: $color-primary-500;
  background-repeat: no-repeat;
  background-image: url(../img/background/promo-small.jpg), linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);

  &__heading {
    font-family: $font-heading;
   @include set-text(36px, 1);
  }

  &__text {
    font-family: $font-heading;
    line-height: 1;
    text-transform: uppercase;
  }

  &__button {
    @include button(14px, 18px);

    padding: 8px 44px;
  }

  @media (min-width: $tablet-width) {
    display: block;
    background: white;
    color: #000;

    &__container {
      position: relative;
      padding: 0 90px 526px;
    }

    &__heading {
      @include set-text(60px, 60px);

      text-align: left;
    }

    &__text {
      @include set-text(20px, 20px);

      text-align: left;
    }

    &__image {
      position: absolute;
      bottom: 0;
      left: 0;
    }

    &__button {
      @include button(20px, 20px);

      width: auto;
      padding: 14px 26px;
    }
  }

  @media (min-width: $desktop-width) {
    background: transparent;

    &::after {
      content: "";
      position: absolute;
      top: -200px;
      right: 0;
      bottom: 0;
      left: 50%;
      z-index: -2;
      background-color: #63a63b;
      background-position: 0 100%;
      background-repeat: no-repeat;
      background-image: url(../img/background/promo-large.jpg);
    }

    &__container {
      position: relative;
      min-height: 582px;
      padding: 20px 610px 95px 80px
    }
  }
}
