

.page-header {
  &__container {
    width: 320px;
    margin: 0 auto;
    padding: 12px 20px 13px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    align-items: center;
  }

  &__logo {
    grid-column: 1/3;
  }

  &__menu {
    display: none;
  }

  &__toggler {
    width: 24px;
    height: 18px;
    background: url("../../img/burger.svg") no-repeat center;
  }

  @media (min-width: $tablet-width) {
    &__container {
      display: block;
    }

    &__nav {
      display: grid;
      grid-template-columns: 174px 1fr;
      gap: 93px;
    }

    &__menu {
      width: 440px;
      display: flex;
      align-items: center;
      gap: 30px;
      justify-content: flex-end;
      margin: 0 auto;

      @include set-text(18px, 24px);
    }

    &__toggler {
      display: none;
    }
  }


  @media (min-width: $desktop-width) {
    &__nav {
      padding: 53px 110px 0 100px;
    }

    &__menu {
      width: 660px;
      @include set-text(20px, normal);
    }
  }
}
