@import "../variables";

.site-menu {
  @include reset;

  font-family: $font-heading;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  list-style: none;

  &__link {
    color: $color-black;

    text-decoration: none;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: flex-end;

    &__link {
      color: #fff;
    }
  }
}
